.about-us {
  .about-us-content {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 170px 0;
    position: relative;

    @media screen and (max-width: 1350px) {
      justify-content: center;
    }

    @media screen and (max-width: $media-tablet) {
      flex-direction: column;
      padding: 70px 0;
    }

    .bottom-part {
      display: flex;
      align-items: flex-start;
      margin-top: 70px;
      position: relative;

      @media screen and (max-width: $media-mobile) {
        flex-direction: column;
        margin-top: 35px;
      }

      .heading {
        font-weight: 600;
        font-size: 24px;
        line-height: 20px;
        color: $color-black;
        margin-bottom: 20px;
      }

      .swiper {
        position: static;
      }

      .swiper-wrapper {
        position: static;
      }

      .swiper-our-mission {
        max-width: 260px;
        height: 205px;

        @media screen and (max-width: $media-mobile) {
          max-width: 300px;
          margin: 0;
        }

        .swiper-slide-our-mission {
          svg {
            margin-bottom: 30px;
          }

          .paragraph {
            color: #657790;
            font-size: 14px;
            line-height: 24px;
            font-weight: 400;
            max-width: 260px;

            @media screen and (max-width: $media-mobile) {
              max-width: 100%;
            }
          }
        }

        .swiper-pagination {
          text-align: left;
          top: 190px;
          height: 10px;
        }

        .swiper-pagination-bullet {
          background: #1266e340;
        }

        .swiper-pagination-bullet-active {
          background: $color-blue;
        }
      }

      .reliable-professionals {
        max-width: 390px;
        margin-left: 55px;

        @media screen and (max-width: $media-mobile) {
          margin: 30px 0 0;
          max-width: 100%;
        }

        .paragraph {
          color: #657790;
          font-size: 16px;
          line-height: 27px;
          font-weight: 400;
        }

        .paragraph:last-of-type {
          margin: 15px 0 35px;

          @media screen and (max-width: $media-mobile) {
            margin: 15px 0 25px;
          }
        }
      }
    }

    .about-us-content-img {
      position: relative;

      img {
        width: 100%;
      }

      @media screen and (max-width: $media-tablet) {
        margin-top: 50px;
      }

      .blue-block {
        position: absolute;
        bottom: -41px;
        right: -51px;
        background: #1266e396;
        width: 180px;
        height: 267px;
        display: block;

        @media screen and (max-width: 500px) {
          right: -10px;
          bottom: -10px;
          height: 200px;
          width: 150px;
        }
      }
    }
  }
}
