.our-works {
  .our-works-content {
    padding: 170px 0;
    position: relative;

    @media screen and (max-width: $media-tablet) {
      padding: 70px 0;
    }

    @media screen and (max-width: $media-mobile) {
      padding: 30px 0;
    }

    .swiper-wrapper {
      height: auto;
      position: static;
    }

    .swiper {
      position: static;
    }

    .swiper-our-works {
      .swiper-slide-our-works {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 60px 0 0;

        @media screen and (max-width: $media-tablet) {
          flex-direction: column;
          margin: 30px 0;
        }

        .gallery {
          margin-right: 30px;

          @media screen and (max-width: $media-tablet) {
            margin: 0 0 30px;
          }

          .gallery-top {
            img {
              @media screen and (max-width: $media-mobile) {
                width: 100%;
              }
            }
          }

          .gallery-bottom {
            margin-top: 15px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            img {
              display: flex;
              @media screen and (max-width: $media-mobile) {
                width: 100%;
                display: none;
              }
            }
          }
        }

        .info-block {
          background: #f7f9fb;
          padding: 43px 35px;

          @media screen and (max-width: $media-mobile) {
            padding: 20px 20px;
          }

          .city {
            font-size: 16px;
            line-height: 20px;
            font-weight: 500;
            color: #acacac;

            @media screen and (max-width: $media-mobile) {
              font-size: 14px;
              line-height: 18px;
            }
          }

          .heading {
            font-size: 28px;
            line-height: 34px;
            font-weight: 700;
            color: $color-blue;
            margin: 12px 0 20px;

            @media screen and (max-width: $media-mobile) {
              font-size: 20px;
              line-height: 28px;
              margin: 10px 0 15px;
            }
          }

          .paragraph {
            font-size: 16px;
            line-height: 24px;
            font-weight: 400;
            color: #657790;
            margin-bottom: 13px;

            @media screen and (max-width: $media-mobile) {
              font-size: 14px;
              line-height: 20px;
              margin-bottom: 10px;
            }
          }

          .paragraph:last-of-type {
            margin-bottom: 25px;
          }
        }
      }

      .swiper-button-prev::after,
      .swiper-button-next::after {
        content: "";
      }

      .swiper-button-prev,
      .swiper-button-next {
        top: 185px;
        margin: 0;

        @media screen and (max-width: $media-tablet) {
          top: auto;
          bottom: 0;
        }
      }

      .swiper-button-prev {
        left: auto;
        right: 75px;

        @media screen and (max-width: $media-tablet) {
          left: 0;
          right: auto;
        }
      }

      .swiper-button-next {
        right: 0;

        @media screen and (max-width: $media-tablet) {
          left: 75px;
          right: auto;
        }
      }

      .swiper-button-prev-our-works,
      .swiper-button-next-our-works {
        width: 61px;
        height: 61px;
        background: #e9e9e9;
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        @media screen and (max-width: $media-tablet) {
          display: none;
        }
      }
    }
  }
}
