.footer {
  background: $color-black;
  padding: 70px 0;

  .footer-content {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media screen and (max-width: $media-tablet) {
      flex-direction: column;
    }

    .nav {
      padding-right: 100px;

      @media screen and (max-width: 1890px) {
        padding-right: 50px;
      }

      @media screen and (max-width: 1800px) {
        padding-right: 0px;
      }

      @media screen and (max-width: 1600px) {
        margin: 30px 0;
      }

      @media screen and (max-width: $media-tablet) {
        display: flex;
        flex-direction: column;
        margin: 10px 0;
      }

      //   @media screen and (max-width: $media-mobile) {
      //     display: none;
      //   }

      .item {
        font-size: 16px;
        line-height: 24px;
        color: $color-white;
        font-weight: 600;
        margin-right: 40px;

        @media screen and (max-width: $media-tablet) {
          margin: 30px 0 0;
        }
      }

      .item:last-of-type {
        margin: 0;

        @media screen and (max-width: $media-tablet) {
          margin: 30px 0 0;
        }
      }
    }

    .socials {
      display: flex;
      align-items: center;

      @media screen and (max-width: $media-tablet) {
        margin: 30px 0 0;
      }

      .item {
        margin-right: 35px;
      }

      .item:last-of-type {
        margin: 0;
      }
    }
  }
}
