.our-services {
  background-image: url("./assets/images/our-services/background.png");
  background-size: cover;

  .our-services-content {
    padding: 120px 0;

    @media screen and (max-width: $media-tablet) {
      padding: 80px 0;
    }

    @media screen and (max-width: $media-mobile) {
      padding: 60px 0;
    }

    &__top {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @media screen and (max-width: $media-mobile) {
        flex-direction: column;
        align-items: flex-start;
      }

      &_right-part {
        .paragraph {
          font-size: 16px;
          line-height: 24px;
          font-weight: 400;
          color: $color-white;
          max-width: 390px;
          margin-top: 20px;

          @media screen and (max-width: $media-mobile) {
            max-width: 100%;
            margin-top: 15px;
          }
        }
      }
    }

    &__main {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 60px 0;

      @media screen and (max-width: $media-tablet) {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 20px;
        margin: 30px 0;
      }

      @media screen and (max-width: $media-mobile) {
        grid-template-columns: 1fr 1fr;
        margin: 0 0 30px;
      }

      @media screen and (max-width: 500px) {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
      }

      &_item {
        position: relative;
        width: 222px;
        height: 346px;
        text-align: center;
        padding: 190px 8px 40px;
        transition: 0.3s;

        @media screen and (max-width: 500px) {
          margin-top: 30px;
        }

        .item-bg {
          position: absolute;
          left: 0;
          top: 0;
          z-index: 1;
          width: 100%;
        }

        .info {
          position: relative;
          z-index: 2;
        }

        .heading {
          color: $color-white;
          margin-top: 25px;
          text-transform: uppercase;
        }

        .item-hover {
          opacity: 0;
          position: absolute;
          left: 0;
          top: 0;
          background: #1266e3cc;
          z-index: 3;
          visibility: hidden;
          transition: 0.3s;
          width: 222px;
          height: 346px;
          padding: 50px 16px 60px;

          .paragraph {
            color: $color-white;
            font-size: 14px;
            line-height: 21px;
            font-weight: 500;
            margin-top: 15px;
          }
        }

        &:hover {
          .info {
            display: none;
          }

          .item-hover {
            opacity: 1;
            visibility: visible;
          }
        }
      }
    }

    .button-blue {
      margin: 0 auto;
    }
  }
}
