.reviews {
  background: #f7f9fb;
  padding: 120px 0;

  @media screen and (max-width: $media-tablet) {
    padding: 60px 0;
  }

  @media screen and (max-width: $media-mobile) {
    padding: 30px 0;
  }

  .reviews-content {
    text-align: center;

    .title-black {
      margin: 15px auto 0;

      @media screen and (max-width: $media-mobile) {
        margin: 10px auto 0;
      }
    }
  }

  .swiper-wrapper {
    height: 720px;
  }

  .reviews-swiper {
    margin-top: 60px;

    @media screen and (max-width: $media-mobile) {
      margin-top: 30px;
    }

    .swiper-slide-reviews-swiper {
      position: relative;

      .bg-img {
        position: absolute;
        display: block;
        left: 0;
        top: 0;
        z-index: -1;
        width: 100%;
      }

      .main-block {
        background-color: $color-white;
        padding: 32px;
        width: 612px;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        top: 256px;

        .heading {
          font-size: 20px;
          line-height: 24px;
          font-weight: 600;
          color: $color-black;
        }

        .paragraph {
          font-size: 16px;
          line-height: 24px;
          font-weight: 400;
          color: #657790;
          margin: 16px 0 24px;
        }

        .bottom-part {
          display: flex;
          align-items: center;

          .info {
            font-weight: 500;
            font-size: 14px;
            line-height: 22px;

            .name {
              color: #0e1735;
            }

            .city {
              color: #657790;
            }
          }
        }
      }
    }
  }
}
