.header {
  background-image: url("./assets/images/header/background.png");
  background-size: cover;

  .header-top-wrapper {
    padding: 29px 72px 31px 50px;
    border-bottom: 1px solid #8c8c8c8a;

    @media screen and (max-width: $media-tablet) {
      padding: 30px;
    }

    @media screen and (max-width: $media-mobile) {
      padding: 20px;
    }

    &__content {
      display: flex;
      align-items: center;
      justify-content: space-between;

      @media screen and (max-width: 1600px) {
        flex-direction: column;
      }

      @media screen and (max-width: $media-mobile) {
        flex-direction: row;
      }

      &_logo {
        img {
          @media screen and (max-width: $media-mobile) {
            width: 200px;
          }
        }
      }

      &_nav {
        padding-right: 220px;

        @media screen and (max-width: 1890px) {
          padding-right: 150px;
        }

        @media screen and (max-width: 1800px) {
          padding-right: 50px;
        }

        @media screen and (max-width: 1700px) {
          padding-right: 0;
        }

        @media screen and (max-width: 1600px) {
          margin: 30px 0;
        }

        @media screen and (max-width: $media-mobile) {
          display: none;
        }

        .item {
          font-size: 16px;
          line-height: 24px;
          color: $color-white;
          font-weight: 600;
          margin-right: 40px;
          transition: 0.3s;
          padding-bottom: 48px;

          &:hover {
            border-bottom: 5px solid $color-blue;
          }
        }

        .item:last-of-type {
          margin: 0;
        }
      }

      &_right-menu {
        display: flex;

        @media screen and (max-width: $media-mobile) {
          display: none;
        }

        .button-position {
          margin-right: 31px;

          .button-price {
            background: $color-blue;
            font-size: 16px;
            line-height: 24px;
            color: $color-white;
            font-weight: 600;
            padding: 18px 30px;
          }
        }

        .contact {
          display: flex;
          align-items: center;

          .icon {
            border: 2px solid $color-blue;
            width: 60px;
            height: 60px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 5px;

            span {
              width: 49px;
              height: 49px;
              display: flex;
              align-items: center;
              justify-content: center;
              background: $color-white;
              border-radius: 50%;
            }
          }

          .info {
            margin-left: 15px;

            .item {
              display: block;
              font-size: 16px;
              line-height: 24px;
              color: $color-white;
              font-weight: 600;
            }

            .item:last-of-type {
              font-size: 13px;
              line-height: 19.5px;
              font-weight: 400;
              margin-top: 5px;
            }
          }
        }
      }
    }
  }

  .header-main {
    padding: 160px 0 273px;

    @media screen and (max-width: 1600px) {
      padding: 100px 0 200px;
    }

    @media screen and (max-width: $media-mobile) {
      padding: 30px 0 60px;
    }

    .header-title {
      font-size: 70px;
      line-height: 77px;
      font-weight: 700;
      color: $color-white;
      max-width: 810px;

      @media screen and (max-width: 1600px) {
        font-size: 50px;
        line-height: 55px;
      }

      @media screen and (max-width: $media-mobile) {
        font-size: 28px;
        line-height: 38px;
        max-width: 410px;
      }
    }

    .header-paragraph {
      margin: 40px 0;
      font-size: 18px;
      line-height: 27px;
      font-weight: 500;
      color: $color-white;
      max-width: 523px;

      @media screen and (max-width: 1600px) {
        margin: 30px 0;
      }

      @media screen and (max-width: $media-mobile) {
        margin: 20px 0;
        font-size: 16px;
        line-height: 25px;
      }
    }

    .button-positon {
      display: flex;
      align-items: center;

      @media screen and (max-width: $media-mobile) {
        flex-direction: column;
        align-items: flex-start;
      }

      .button-white {
        padding: 25px 35px;
        background: $color-white;
        color: $color-black;
        font-size: 16px;
        line-height: 24px;
        font-weight: 600;
        margin-left: 15px;

        @media screen and (max-width: $media-mobile) {
          padding: 15px 25px;
          font-size: 14px;
          line-height: 22px;
          margin: 15px 0 0;
        }
      }
    }
  }
}
