* {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  text-decoration: none;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

html,
body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
}

ol,
ul {
  list-style: none;
  font-family: "Montserrat", sans-serif;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  font-family: "Montserrat", sans-serif;
}

.container {
  width: 1230px;
  margin: 0 auto;

  @media screen and (max-width: $media-tablet) {
    width: 100%;
    padding: 0 20px;
  }
}

.button {
  cursor: pointer;
  border: none;
  outline: none;
  font-family: "Montserrat", sans-serif;
}

.button-blue {
  padding: 25px 35px;
  background: $color-blue;
  color: $color-white;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  display: flex;
  align-items: center;
  justify-content: center;

  @media screen and (max-width: $media-mobile) {
    padding: 15px 25px;
    font-size: 14px;
    line-height: 22px;
  }

  svg {
    margin-right: 8px;
  }
}

.breadcrumbs {
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  color: $color-blue;

  @media screen and (max-width: $media-mobile) {
    font-size: 14px;
    line-height: 20px;
  }
}

.title {
  font-weight: 700;
  font-size: 40px;
  line-height: 48px;
  margin-top: 15px;
  max-width: 603px;

  @media screen and (max-width: $media-mobile) {
    font-size: 24px;
    line-height: 30px;
    margin-top: 10px;
  }
}

.title-black {
  color: $color-black;
}

.title-white {
  color: $color-white;
}

.mobile-menu {
  background: $color-blue;
  padding: 20px;
  display: block;
  transform: translateX(-100%);
  transition: transform 0.5s;
  position: fixed;
  width: 100%;
  overflow: auto;
  height: 100%;
  right: 0;
  top: 0;
  z-index: 998;

  .mobile-top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .logo-mobile {
      img {
        width: 200px;
      }
    }
  }

  .nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 30px;

    .item {
      font-size: 16px;
      line-height: 24px;
      color: $color-white;
      font-weight: 600;
      margin-top: 20px;
    }
  }

  .contact {
    display: flex;
    align-items: center;
    margin-top: 30px;

    .icon {
      border: 2px solid $color-white;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 5px;

      span {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: $color-white;
        border-radius: 50%;
      }
    }

    .info {
      margin-left: 15px;

      .item {
        display: block;
        font-size: 16px;
        line-height: 24px;
        color: $color-white;
        font-weight: 600;
      }

      .item:last-of-type {
        font-size: 13px;
        line-height: 19.5px;
        font-weight: 400;
        margin-top: 5px;
      }
    }
  }
}

.mobile-menu.open {
  transform: translateX(0);
}

.burger {
  width: 25px;
  height: 20px;
  position: relative;
  background: transparent;
  border: none;
  display: none;
  transition: all 0.3s;
  z-index: 6;

  @media screen and (max-width: $media-mobile) {
    display: block;
  }

  &__line {
    width: 100%;
    height: 2px;
    background: $color-blue;
    position: absolute;
    left: 0;
    transition: all 0.3s;
    border-radius: 50px;
  }

  &__line_top {
    top: 0;
  }

  &__line_middle {
    top: 50%;
    transform: translateY(-50%);
  }

  &__line_bottom {
    bottom: 0;
  }
}

.burger.is-active {
  z-index: 999;
  position: absolute;
  top: 35px;
  right: 20px;

  .burger__line_top {
    transform: rotate(-45deg);
    top: 0;
    left: 0;
    background: #fff;
    z-index: 999;
    position: absolute;
  }

  .burger__line_middle {
    display: none;
  }

  .burger__line_bottom {
    transform: rotate(45deg);
    top: 0;
    left: 0;
    background: #fff;
    z-index: 999;
    position: absolute;
  }
}

.fixed-btn-block {
  z-index: 11;
  position: fixed;
  right: 65px;
  bottom: 50px;

  @media screen and (max-width: 768px) {
    right: 20px;
  }

  .fixed-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background: $color-blue;
    width: 79px;
    height: 76.69px;
    border-radius: 50%;

    @media screen and (max-width: $media-mobile) {
      width: 50px;
      height: 47px;
    }

    .fixed-btn-inner {
      max-width: 70px;
      max-height: 70px;
      animation: conctact-icon-animation 1.5s ease 0s 1 normal none running;
      animation-iteration-count: infinite;

      &::before {
        content: " ";
        display: block;
        position: absolute;
        left: -20px;
        right: -20px;
        top: -20px;
        bottom: -20px;
        border-radius: 50%;
        animation: animate 1.5s linear infinite;
        opacity: 0;
        background: #1266e31a;
        z-index: -1;
      }

      &::after {
        content: " ";
        display: block;
        position: absolute;
        left: -20px;
        right: -20px;
        top: -20px;
        bottom: -20px;
        border-radius: 50%;
        animation: animate 1.5s linear infinite;
        opacity: 0;
        background: #1266e31a;
        z-index: -1;
      }

      img {
        max-width: 100%;
        height: auto;

        @media screen and (max-width: $media-mobile) {
          width: 20px;
        }
      }
    }
  }
}

@keyframes conctact-icon-animation {
  0% {
    transform: rotate(0);
  }
  10% {
    transform: rotate(-30deg);
  }
  20% {
    transform: rotate(15deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(7.5deg);
  }
  50% {
    transform: rotate(-6deg);
  }
  60% {
    transform: rotate(5deg);
  }
  70% {
    transform: rotate(-4.28571deg);
  }
  80% {
    transform: rotate(3.75deg);
  }
  90% {
    transform: rotate(-3.33333deg);
  }
  100% {
    transform: rotate(0);
  }
}

@keyframes animate {
  0% {
    transform: scale(1.5);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1.8);
    opacity: 0;
  }
}
