.contact-us {
  background: #f7f9fb;
  padding: 120px 0;

  @media screen and (max-width: $media-tablet) {
    padding: 60px 0;
  }

  @media screen and (max-width: $media-mobile) {
    padding: 30px 0;
  }

  .contact-us-content {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    @media screen and (max-width: $media-mobile) {
      flex-direction: column;
    }

    .text {
      .title-black {
        max-width: 478px;
      }

      .block-bottom {
        max-width: 300px;
        margin-top: 20px;

        .item {
          display: flex;
          align-items: center;
          margin-top: 32px;

          a {
            color: #657790;
            font-size: 18px;
            line-height: 32px;
            font-weight: 400;
          }

          .icon {
            width: 36px;
            height: 36px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            background: #1266e31a;
            margin-right: 20px;
          }
        }

        .cities {
          display: flex;
          align-items: flex-start;

          .name {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 15px 45px;

            span {
              color: #657790;
              font-size: 18px;
              line-height: 22px;
              font-weight: 400;
            }
          }
        }
      }
    }

    .form {
      width: 600px;

      @media screen and (max-width: 1100px) {
        width: 400px;
      }

      @media screen and (max-width: $media-mobile) {
        width: 100%;
        margin-top: 30px;
      }

      .input-wrapper {
        display: flex;
        flex-direction: column;
        margin-bottom: 24px;

        @media screen and (max-width: $media-mobile) {
          margin-bottom: 15px;
        }

        .textarea {
          resize: none;
          height: 120px;
        }

        .heading-form {
          font-size: 20px;
          line-height: 20px;
          font-weight: 500;
          color: $color-black;

          @media screen and (max-width: $media-mobile) {
            font-size: 18px;
          }
        }

        .input {
          padding: 17px 20px;
          width: 100%;
          font-size: 14px;
          line-height: 24px;
          color: #65779061;
          font-weight: 400;
          border-radius: 8px;
          border: 1px solid #dbdbdb;
          outline: none;
          margin-top: 20px;

          @media screen and (max-width: $media-mobile) {
            margin-top: 15px;
          }

          &::placeholder {
            font-size: 14px;
            line-height: 24px;
            color: #65779061;
            font-weight: 400;
          }
        }
      }

      .input-wrapper:last-of-type {
        margin-bottom: 32px;
      }
    }
  }
}
