.faq {
  padding: 170px 0;

  @media screen and (max-width: $media-tablet) {
    padding: 70px 0;
  }

  @media screen and (max-width: $media-mobile) {
    padding: 30px 0;
  }

  .faq-content {
    text-align: center;
    .title-black {
      margin: 15px auto 40px;

      @media screen and (max-width: $media-mobile) {
        margin: 10px auto 10px;
      }
    }

    .faq-item {
      will-change: height;
      overflow-y: hidden;
      position: relative;
      padding: 20px 31px 23px 31px;
      cursor: pointer;
      background: #ffffff;
      box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.08);
      border-radius: 15px;
      width: 100%;
      margin-top: 20px;
      transition: 0.3s;

      @media screen and (max-width: $media-mobile) {
        padding: 20px 10px 23px 10px;
      }

      .faq-top {
        position: relative;
        margin: 0 0 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .heading {
          font-weight: 600;
          font-size: 20px;
          color: $color-black;
          line-height: 24px;

          @media screen and (max-width: 1000px) {
            font-size: 16px;
            line-height: 20px;
          }

          @media screen and (max-width: $media-mobile) {
            font-size: 13px;
            line-height: 16px;
            margin-right: 20px;
          }
        }
      }

      .answer {
        font-weight: 400;
        font-size: 16px;
        color: #657790;
        line-height: 29px;
        text-align: start;

        @media screen and (max-width: 1000px) {
          font-size: 14px;
          line-height: 18px;
        }

        @media screen and (max-width: $media-mobile) {
          font-size: 13px;
          line-height: 16px;
        }
      }

      .answer-last {
        margin-top: 20px;
      }
    }

    .faq-item.open {
      .close {
        transform: rotate(180deg);

        svg {
          path {
            stroke: $color-blue;
          }
        }
      }
    }
  }
}
