.partners {
  background: #f7f9fb;

  .partners-content {
    padding: 35px 0 55px;
    display: flex;
    align-items: center;
    justify-content: space-around;

    @media screen and (max-width: $media-mobile) {
      padding: 35px 45px;
    }

    @media screen and (max-width: 600px) {
      flex-direction: column-reverse;
      padding: 5px 25px 35px;
    }

    .item {
      @media screen and (max-width: 600px) {
        margin-top: 30px;
      }
    }
  }
}
